import { Injectable } from '@angular/core';
import { Apollo } from 'apollo-angular';
import { getUsers } from '../gql-operations/query';
import { isArchakaRegistered } from '../gql-operations/query';
import { map } from 'rxjs/operators';
import { RegisterUser } from '../pojo/register.interface';
import { registerUser } from '../gql-operations/mutation';
import { ArchakaInformation } from '../pojo/archakaInforation';
import { archakaInformation } from '../gql-operations/mutation';


@Injectable({
  providedIn: 'root'
})
export class GraphApiService {

  constructor(private apollo: Apollo) { }

  // Lista de usuarios
  getUsers() {
    return this.apollo
    .watchQuery(
      {
        query: getUsers,
        fetchPolicy: 'network-only'
      }
    ).valueChanges.pipe(map((result: any) => {
      return result.data.users;
    }));
  }

  isArchakaRegistered(id: string) {
    return this.apollo
    .watchQuery(
      {
        query: isArchakaRegistered,
        variables: {
          id
        },
        fetchPolicy: 'network-only'
      }
    ).valueChanges.pipe(map((result: any) => {
      return result.data.archaka_information;
    }));
  }

  register(data: RegisterUser) {
    return this.apollo
      .mutate({
        mutation: registerUser,
        variables: {
          data
        }
      });
  }

  upsertArchakaInformation(data: ArchakaInformation) {
    return this.apollo
      .mutate({
        mutation: archakaInformation,
        variables: {
          data
        }
      });
  }



}
