import gql from 'graphql-tag';

export const getUsers = gql`
query {
  user {
    _id
		accessLevel
		district
		mandal
		name
		role
  }
}
`;


export const isArchakaRegistered = gql`
query archaka_information($id: String!) {
	archaka_information (query: {_id: $id })  {
		_id
		name
		templeName
		lastUpdatedby
		lastUpdatorId
  }
}
`;
