import { Injectable } from '@angular/core';
import { FormGroup } from '@angular/forms';
import * as aadhaarValidator from 'aadhaar-validator';
import { ValidatorFn, AbstractControl, FormArray } from '@angular/forms';


@Injectable({
  providedIn: 'root'
})
export class ValidationService {

  userAadhaarNumber: String;
  constructor() { }

  validateAadhaar(aadhaarNumber: string) {
    return (formGroup: FormGroup) => {
      const aadhaarControl = formGroup.controls[aadhaarNumber];

      this.userAadhaarNumber = aadhaarControl.value;

      if (!aadhaarControl) {
        return null;
      }

      if (aadhaarControl.errors && !aadhaarControl.errors.inValid) {
        return null;
      }

      if (aadhaarControl.value.replace(/\s/g, "").length < 12 || aadhaarControl.value.replace(/\s/g, "").length > 12) {
        aadhaarControl.setErrors({ inValid: true });
      } else {
        aadhaarControl.setErrors(null);
      }


      if (!aadhaarValidator.isValidNumber(aadhaarControl.value.replace(/\s/g, ""))) {
        aadhaarControl.setErrors({ inValid: true });
      } else {
        aadhaarControl.setErrors(null);
      }
    }
  }


  // max length
  maxLength(max: number): ValidatorFn | any {
    console.log("I am here");
    return (control: AbstractControl[]) => {
        if (!(control instanceof FormArray)) return;
        return control.length > max ? { maxLength: true } : null;
    }
}

// min length
minLength(min: number): ValidatorFn | any {
    return (control: AbstractControl[]) => {
        if (!(control instanceof FormArray)) return;
        return control.length < min ? { minLength: true } : null;
    }
}

}
