import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { userPrivileges } from "../pojo/userPrivileges.model";
import {userIPAddress} from "../pojo/userIPAddress.model";
import { userAccessTokenDTO } from "../pojo/user-jwt.model";
import { District } from "../pojo/district.model";

@Injectable({
  providedIn: 'root'
})
export class RestFulService {

  public DistrictList:District[];

  constructor(private http: HttpClient) { }


  userAccessUri = "https://webhooks.mongodb-realm.com/api/client/v2.0/app/dev-archaca-riagy/service/getUserAccessDetails/incoming_webhook/getUsersDetails";
  jwtTokenUri = "https://webhooks.mongodb-realm.com/api/client/v2.0/app/dev-archaca-riagy/service/getUserAccessDetails/incoming_webhook/getJwtToken"
  aptUri = "https://raw.githubusercontent.com/archaka-infobank/api/master/docs/AndhraPradesh/";

  requestHeaders = new HttpHeaders()
    .set("Content-Type", "application/json");

  

  async getUserDetails(aadhaarNumber: number) {
    return await this.http.post<userPrivileges>(this.userAccessUri, '{"aadhaarNumber":"' + aadhaarNumber + '"}', { 'headers': this.requestHeaders }).toPromise();
  }

  async getJwtToken(aadhaarNumber: number, password : string, ipAddress : String, userName:String) {
   const input =  '{"aadhaarNumber":"'+aadhaarNumber+'","ipAddress":"'+ipAddress+'","userName":"'+userName+'", "apikey":"'+btoa(password)+'"}'; 
    
    return await this.http.post<userAccessTokenDTO>(
      
      this.jwtTokenUri, input ,
      {'headers': new HttpHeaders()
    .set("Content-Type", "application/json")})
    .toPromise();
  }

  async getIP() {
    return await this.http.get<userIPAddress>('https://jsonip.com/').toPromise();
}


async getDistricts() {
  return await this.http.get<District[]>(this.aptUri+'districts.json').toPromise();
}

getMandals(dist:number): Observable<District[]>  {
return  this.http.get<District[]>(this.aptUri+'district-'+dist+'/mandals.json');
}

getVillages(dist:number, mandal:number): Observable<District[]>  {
  return  this.http.get<District[]>(this.aptUri+'district-'+dist+'/mandal-'+mandal+'/villages.json');
  }

} 
